export class ContactService {
    send(contact_obj) {
        const api_url = "https://api.skp-art.com/post" // TODO per artista
        return fetch(api_url, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
            },
            body: JSON.stringify(contact_obj),
        })
    }
}
