import * as React from "react"
import { useState } from "react"
import Button from "../Button/Button"
import { ContactStyles } from "./ContactStyles"
import { changeState, sendForm } from "../../form"

const Contact = ({ textareaTemplate, onSuccess }) => {
    const [state, setState] = useState({
        email: "",
        name: "",
        message: textareaTemplate,
        errors: [],
        success: false,
    })

    const requiredFields = ["message", "name", "email"]

    function valueChanged(e) {
        changeState(setState, { [e.target.name]: e.target.value })
    }

    function _sendForm(e) {
        return sendForm(e, requiredFields, state, setState, "contact-form", onSuccess)
    }

    return (
        <ContactStyles>
            <form name="contact" id="contact-form">
                <input placeholder="Your name..." type="text" name="name" onChange={valueChanged} />
                <input placeholder="Your email..." type="email" name="email" onChange={valueChanged} />
                <textarea
                    placeholder="Your message..."
                    name="message"
                    rows="5"
                    defaultValue={textareaTemplate || ""}
                    onChange={valueChanged}
                />
                <Button text="Send Message" onClick={_sendForm} />
            </form>
        </ContactStyles>
    )
}

export default Contact
