import { isNullOrUndefined, isValidEmail } from "./constants/globals"

export function changeStateTo(setState) {
    return (newPartialState) =>
        setState((prevState) => {
            return Object.assign({}, prevState, newPartialState)
        })
}

export function changeState(setState, newPartialState) {
    changeStateTo(setState)(newPartialState)
}

export function validateFields(requiredFields, state, newErrors, msgGenerator, customValidator) {
    if (isNullOrUndefined(customValidator)) {
        customValidator = (f, v) => true
    }
    for (let i = 0; i < requiredFields.length; i++) {
        const reqField = requiredFields[i]
        if (!state[reqField] || !customValidator(reqField, state[reqField])) {
            console.log("Validation of required field " + reqField + " failed")
            newErrors.push({
                msg: msgGenerator(reqField),
                field: reqField,
            })
        }
    }
    return newErrors
}

export function sendForm(e, requiredFields, state, setState, formId, onSuccess) {
    e.preventDefault()
    //reset element validity
    let formElem = document.getElementById(formId)
    for (const requiredField of requiredFields) {
        formElem.elements[requiredField].setCustomValidity("")
    }
    const newErrors = []
    validateFields(
        requiredFields,
        state,
        newErrors,
        (f) => formId + ".field." + f + ".missing",
        (f, v) => {
            if (f === "email") {
                return isValidEmail(v)
            }
            return true
        }
    )
    changeState(setState, { errors: newErrors, success: false })
    if (newErrors.length > 0) {
        for (const newError of newErrors) {
            const element = formElem.elements[newError.field]
            element.setCustomValidity(newError.msg)
        }
        return false
    }
    console.log("Sending form")
    const kvpairs = {}
    for (let i = 0; i < formElem.elements.length; i++) {
        const e = formElem.elements[i]
        kvpairs[encodeURIComponent(e.name)] = encodeURIComponent(e.value)
    }
    changeState(setState, { success: true })
    onSuccess(state, kvpairs)
    return true
}
