import * as React from "react"
import { useState } from "react"
import Contact from "../components/Contact/Contact"
import { getQueryParameter } from "../constants/globals"
import { Artist } from "../models/artist"
import BannerModule from "../components/BannerModule/BannerModule"
import { getImage } from "gatsby-plugin-image"
import { ArtworkInfo } from "../components/Artwork/ArtworkInfo"
import { ContactService } from "../services"
import Bugsnag from "@bugsnag/js"
import { Alert } from "@mui/material"
import Top from "../components/Top"

const ContactPage = ({ pageContext: { artist: cfArtist } }) => {
    const [state, setState] = useState({
        success: false,
    })

    const artist = Artist.registerFromContentful(cfArtist)
    let contactType = getQueryParameter("type")
    let contactSlug = getQueryParameter("slug")
    let artwork = null
    let bgImage = null
    let textarea = ""
    if (contactType === "artwork") {
        artwork = artist.artworkBySlug(contactSlug)
        bgImage = getImage(artwork.gatsbyImage)
        textarea = "I have a question about the work '" + artwork.title + "' by " + artist.name + "..."
    }

    function onSuccess(state, kvpairs) {
        new ContactService().send(kvpairs).catch((e) => {
            Bugsnag.setContext(JSON.stringify(state))
            Bugsnag.notify(e)
        })
        setState({
            success: true,
        })
    }

    return (
        <Top title="Contact Us" artist={artist}>
            <BannerModule title="Contact Us" subTitle={<ArtworkInfo artwork={artwork} contact />} bgImage={bgImage}>
                {state["success"] ? (
                    <Alert severity="success">
                        Thank you very much for reaching out to us. We will get back to you as soon as possible!
                    </Alert>
                ) : (
                    <Contact textareaTemplate={textarea} onSuccess={onSuccess} />
                )}
            </BannerModule>
        </Top>
    )
}

// noinspection JSUnusedGlobalSymbols
export default ContactPage
